import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'head',
      component: () => import('@/components/header'),
      redirect: '/dash',
      children: [{
        name: 'dash',
        path: '/dash',
        component: () => import('@/views/dashboard')
      } ]
    }, {
      path: '/detail',
      name: 'detailView',
      component: () => import('@/views/detailView')
    }
  ]
})



